import React, { useContext } from "react";

import IconButton from "@mui/material/IconButton";

import { useHistory } from "react-router-dom";

import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import KeyboardBackspaceIcon from "@mui/icons-material/KeyboardBackspace";

import { colors, makeStyles } from "@material-ui/core";
import bootom from "../../bootom.png";
import visa from "../card/visa.png";
import DeleteIcon from "@mui/icons-material/Delete";
import { CardStore } from "../../context/Cardcontext";

let url =
  "https://rukminim1.flixcart.com/image/150/150/kcgk1ow0/headphone/n/u/a/235v2-fast-charging-boat-original-imaftk6us4af7bca.jpeg?q=70";

let userStyle = makeStyles({
  MainCart: {
    display: "flex",
    padding: "50px",
    background: "#eeeeee",
    
    "@media(max-width: 600px)": {
      padding:"10px",
      display:"block"
    },
  },
  Cart: {
    height: "80vh",
    // border: "2px solid red",
    flex: 2,
    background: "white",
    contain:"content",
    content:"contain",
    
    "@media(max-width: 600px)": {
    // border:'2px solid blue',
    
      
    },
  },
  PaymetCart: {

    height: "70vh",
    // border: "2px solid yellow",

    background: "white",
    
    "@media(max-width: 600px)": {
      // border:"2px solid red"
    },
  },
  cartHeader: {
    // '&>div':{
    //   marginLeft:"10px",
    //   marginTop:"20px",
    //   display:"flex",
    //   // border:"2px solid red"
    //   alignItems:"center",
    // }
  },
  DivShoping0: {
    display: "flex",
    alignItems: "center",
    padding: "10px",
    // border:'2px solid red',
  },
  DivShoping: {
    // border:"2px solid blue",
    display: "flex",
    flexDirection: "column",

    // border:"2px solid red",
    height: "8vh",
    content: "contain",
    contain: "content",
    "&>div": {
      display: "flex",
      justifyContent: "space-between",
      marginLeft: "20px",
    },
    "&>p": {
      marginLeft: "20px",
    },
  },
  cartIteam: {
    borderRadius: "20px",
    boxShadow: "rgba(0, 0, 0, 0.24) 0px 3px 8px",

    maxWidth: "90%",
    content:"contain",
    contain:'content',

    display: "flex",
    justifyContent: "space-around",
    marginTop: "10px",
    marginLeft: "5%",
    
    
    "@media(max-width: 600px)": {
      // border:"2px solid red",
      display: "block",

    },


    "&>div": {
      // border: "2px solid blue",
    },

    "&>div>h1": {
      fontSize: "20px",
      fontWeight: 600,
    },
  },
  cartdiv1: {
    // objectFit:' contain',
    width: "auto",

    height: "auto",
    marginRight: "30%",
    padding: "10px",
    borderRadius: "20px",
    // overflow:'hidden',
    display: "flex",
    // border: "2px solid blue",
    "@media(max-width: 600px)": {
      // border:"2px solid red",
      width: "100%",
      
      
    },

    "&>img": {
      objectFit: "contain",
      width: "90px",
      height: "90px",
      // border:'2px solid red',
      borderRadius: "20px",
    },
    "&>div": {
      marginLeft: "25px",
    },
    "&>div>p": {
      marginTop: "16px",
      // marginLeft:'10px',
      fontSize: "1rem",
    },
    "&>div>h1": {
      marginTop: "5px",
      // marginLeft:'10px',
      fontSize: "1.3rem",
      color: "#4f4f4f",
    },
  },
  cartdiv2: {
    marginLeft: "20px",
  },
  PriceBox: {
    display: "flex",

    alignItems: "center",
    justifyContent: "space-evenly",

    width: "20%",
    // border: "2px solid red",
    "@media(max-width: 600px)": {
      // border:"2px solid red",
      marginLeft:"60%"
    },
    
  },
  CartDiv:{
    
      
  },
  // RIGHT PANEL
  PaymetCart: {
    color: "white",
    background: "#3b71ca",
    height: "80vh",
    contain: "content",
    content: "contain",
    border:"1px solid grey",
    "@media(max-width: 600px)": {
      
    },

    flex: 1,
  },
  PaymetCart_div1: {
    display: "flex",
    // border: "2px solid yellow",

    justifyContent: "space-between",
    padding: "20px",
    fontSize: "20px",
    
    "@media(max-width: 600px)": {
      // border:'2px solid red',
      padding:"10px"
    },
  },
  PaymetCart_div2: {


    // border: "2px solid yellow",
    // padding: "10px",
     
    "@media(max-width: 600px)": {
      marginTop:"1px"
     },
    "&>img": {
      marginLeft: "10px",
      
      "@media(max-width: 600px)": {
       marginTop:"1px"
      },
    },
  },
  PaymetCart_div3: {
    marginTop: "10px",
    // border:"2px solid red",
    height: "54.5vh",
    
    contain: "content",
  },
  FirstInut: {
    marginLeft: "10px",
    marginTop: "20px",
    // border:'2px solid red',
     
    "@media(max-width: 600px)": {
      // border:"2px solid red",
      marginTop: "0px",
      padding:"0"
     },

    padding: "10px",
  },
  FirstInut1: {
    width: "96%",
    height: "20px",
     
    "@media(max-width: 600px)": {
      
      
     },
  },
  SecondInut: {

    // border:'2px solid red',
    marginLeft: "15px",
    padding: "10px",
    marginTop: "10px",
    "@media(max-width: 600px)": {
      // border:"2px solid red",
      display:"flex",
      alignItems:"center",
      padding: "10px",
      
     },
   
  },
  BTNDiv: {
    // border:"2px solid red",
    "@media(max-width: 600px)": {
      // border:"2px solid red"
      
     },
    
    padding: "10px",
    "&>div": {
      // border:"2px solid blue",
      display: "flex",
      justifyContent: "space-between",
      width: "85%",
      marginLeft: "13px",
    },

    "&>p": {
      marginTop: "10px",
      marginLeft: "20px",
      fontSize: "12px",
      marginLeft: "10px",
    },
  },
});

function Cart() {
  let History = useHistory();

  const INR = new Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "INR",
  });
  let { cartData,setDartdata } = useContext(CardStore);
  let classes = userStyle();
  console.log(cartData, "map");


  function RemoveIteam(iteam){
    // console.log(iteam.id)
let updated = cartData.filter((data)=>{
      return data.id!=iteam.id;
    })
    setDartdata(updated)

  }

  return (
    <>
      <div className={classes.MainCart}>
        <div className={classes.Cart}>
          {/* Header */}
          <div className={classes.cartHeader}>
            <div className={classes.DivShoping0}>
              <KeyboardBackspaceIcon
                style={{ cursor: "pointer" }}
                onClick={(e) => {
                  History.goBack();
                }}
              />{" "}
              Continue Shopping
            </div>

            <hr></hr>
            <div className={classes.DivShoping}>
              <div>
                <p style={{ color: "grey" }}>Shopping Cart </p>

                <div> </div>
              </div>

              <p style={{ color: "grey", lineHeight: "5px" }}>
                You have {cartData.length} Iteam in your cart
              </p>
            </div>
          </div>

          {/* HeaderEND */}

          {/* cartiteam */}

<div style={{overflow:'auto', height:"70%",  contain:"content", content:'contents', scrollbarWidth:' thin',}} className="CartDiv">
          {cartData.length >= 1 ? (
            cartData.map((iteam) => {
              return (
                <>
                  <div className={classes.cartIteam}>
                    <div className={classes.cartdiv1}>
                      <img src={iteam.url} />

                      <div>
                        <h1> {iteam.title.longTitle.slice(0, 10)} </h1>
                        <p>{iteam.rating}</p>
                      </div>
                    </div>

                    <div className={classes.PriceBox}>
                      <h1 style={{ fontSize: "20px", color: "#4f4f4f" }}>
                        {" "}
                        {INR.format(iteam.price.cost)}{" "}
                      </h1>

                      <IconButton aria-label="delete" size="large">
                        <DeleteIcon fontSize="inherit"  onClick={(e)=>{RemoveIteam(iteam)}}  />
                      </IconButton>
                    </div>
                  </div>
                </>
              );
            })
          ) : (
            <h1 style={{ marginLeft: "30px" }}>Cart Is Empty</h1>
          )}

</div>
          {/* cartiteamEND */}
        </div>

        <div className={classes.PaymetCart}>
          <div className={classes.PaymetCart_div1}>
            <h1 style={{ fontSize: "20px" }}>Card detail</h1>
            <img
              src={bootom}
              width={90}
              height={60}
              alt=""
              style={{ borderRadius: "20px" }}
            />
          </div>
          <div className={classes.PaymetCart_div2}>
            <img src={visa} width={200} height={80} alt="" />
          </div>

          <div
            className={classes.PaymetCart_div3}
            style={{ background: "white", color: "black" }}
          >
            <div className={classes.FirstInut}>
              <TextField
                id="outlined-basic"
                label="Name"
                variant="outlined"
                className={classes.FirstInut1}
                style={{ marginTop: "20px" }}
              />
              <br />
              <TextField
                id="outlined-number"
                label="CardNumber"
                type="number"
                style={{ marginTop: "50px", width: "96%" }}
                InputLabelProps={{
                  shrink: true,
                }}
              />
            </div>
            <div className={classes.SecondInut}>
              <TextField
                id="standard-basic"
                size="small"
                label="Expiration"
                variant="standard"
                style={{ width: "40%" }}
              />
              <TextField
                size="small"
                style={{ width: "40%", marginLeft: "70px" }}
                id="outlined-password-input"
                label="CVV"
                type="password"
                autoComplete="current-password"
              />
            </div>

            <div className={classes.BTNDiv}>
              <div>
                <p>Total Iteam</p>
                <p>{cartData.length}</p>
              </div>
              <div>
                <p>total Price</p>
                <p>
                  {INR.format(
                    cartData.reduce(
                      (total, cureent) => {
                        return total + cureent.price.cost;
                      },

                      0
                    )
                  )}
                </p>
              </div>

              <Button
                style={{ marginLeft: "30%", width: "40%" }}
                variant="contained"
              >
                Order
              </Button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Cart;
